	module.exports = {"data":{"markdownRemark":{"html":"<p>I august 1890 sendte Grieg nye bearbeidelser med sanger i pianoversjon til forlegger Peters i Leipzig. Fem år etter den forrige samlingen dukket han igjen ned i tidligere romanse-produksjon, og plukket fram et utvalg på seks sanger. Samlingen rammes inn av «Modersorg» og «Du gamle mor», og inneholder sanger fra kjente opus som Fiskerjenten opus 21 (Bjørnson) med «Det første møte» og Hjertets melodier opus 5 (H. C. Andersen) med «Du fatter ei bølgernes evige gang». Som i den forrige samlingen (opus 41) med «Jeg elsker deg» har han tatt med en «highlight», her med «Solveigs sang» fra Peer Gynt.</p>\n<p>I disse bearbeidelsene har Grieg tatt i bruk sine pianistiske kunnskaper, og evner å gjenskape romansene på pianoets egne premisser. Stykker fra begge opusene har fått plass på mange pianisters repertoar.</p>\n<p>Sangbearbeidelsene ble til om sommeren, som ble tilbragt på Troldhaugen, der han var i perioden april til oktober. I august dro han med Frants Beyer på en to ukers tur i Jotunheimen, og var blant annet på Turtagrø.</p>\n<p>I oktober forlot Nina og Edvard hjembyen og satte kursen mot Kristiania og nye konsertoppdrag. Sent på høsten ankom de København og ble der helt til mai året etter. Like før nyttår var han i bisettelsen til Niels W. Gade, dansk musikklivs store autoritet på 1800-tallet, som 25 år tidligere hadde gitt den unge Grieg «oppdraget» med å skrive en symfoni.</p>\n<p>Foto: Griegsamlingen ved Bergen Offentlige Bibliotek.</p>","timeToRead":1,"excerpt":"I august 1890 sendte Grieg nye bearbeidelser med sanger i pianoversjon til forlegger Peters i Leipzig. Fem år etter den forrige samlingen…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/opus52.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":"Klaverstykker etter egne sanger II","composedin":"1890","startDate":"16.6","startTime":null,"location":"Troldhaugen","artists":["Marianna Shirinyan"],"medialink":null,"title":"52","dedicatedTo":"","author":"Monica Jangaard","date":"2018-05-07T12:00:00.048Z","category":"opus","tags":["klaver"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":2,"src":"/static/opus52-0fb86cad0bc0b4c67c8375bc6c9436e1-47774.jpg","srcSet":"/static/opus52-0fb86cad0bc0b4c67c8375bc6c9436e1-45ad6.jpg 480w,\n/static/opus52-0fb86cad0bc0b4c67c8375bc6c9436e1-e2fcf.jpg 960w,\n/static/opus52-0fb86cad0bc0b4c67c8375bc6c9436e1-47774.jpg 1920w,\n/static/opus52-0fb86cad0bc0b4c67c8375bc6c9436e1-35d2f.jpg 2054w","sizes":"(max-width: 1920px) 100vw, 1920px"}}}},"fields":{"slug":"/opus52/","artist":[{"frontmatter":{"title":"Marianna Shirinyan","category":"artister","tags":["pianist"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/MariannaShirinyan-1fe4a5a06f45826b12bf815675634943-466fa.jpg","srcSet":"/static/MariannaShirinyan-1fe4a5a06f45826b12bf815675634943-27b23.jpg 90w,\n/static/MariannaShirinyan-1fe4a5a06f45826b12bf815675634943-0afbd.jpg 180w,\n/static/MariannaShirinyan-1fe4a5a06f45826b12bf815675634943-466fa.jpg 360w,\n/static/MariannaShirinyan-1fe4a5a06f45826b12bf815675634943-43088.jpg 540w,\n/static/MariannaShirinyan-1fe4a5a06f45826b12bf815675634943-545bd.jpg 720w,\n/static/MariannaShirinyan-1fe4a5a06f45826b12bf815675634943-5b704.jpg 960w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/artister/marianna-shirinyan/","opuses":[{"frontmatter":{"title":"49"}},{"frontmatter":{"title":"52"}}]}}],"opuses":null}}},"pathContext":{"slug":"/opus52/"}}

